import {Model, Module, registModule} from "nodom3";
import { BaseInput } from "./baseinput";
import { UIButton } from './button';

/**
 * list transfer 插件
 * 
 */
export class UIListTransfer extends BaseInput{
    /**
     * 值field
     */
    private __valueField;

    /**
     * 禁用字段名
     */
    private __disableField;

    /**
     * 值
     */
    private __value;

    template(props){
        super.template(props);
        this.__valueField = props.valueField;
        this.__disableField = props.disableField;
        this.__value = props.value;

        let listCtx;
        if(props.template){
            listCtx = props.template;
        }else{
            listCtx = '{{' + props.displayField + '}}';
        }
        let disableCtx = '';
        if (props.disableField) {
            disableCtx = "+ (" + props.disableField + "?' ui-list-item-disable':'')";
        }
        return  `
            <div class="ui-listtransfer">
                <div class='ui-list'>
                    <div x-repeat={{__getLeftRows()}} class={{'ui-list-item' + (__selected?' ui-list-item-active':'')  ${disableCtx} }} 
                        e-click='__clickItem'>
                        <div class='ui-list-itemcontent'>
                            <slot innerRender/>
                        </div>
                        <b class="ui-list-icon"></b>
                    </div>
                </div>
                <div class='ui-listtransfer-btngrp'>
                    <ui-button icon='double-arrow-right' e-click='__rightClick1'/>
                    <ui-button icon='arrow-right' e-click='__rightClick'/>
                    <ui-button icon='arrow-left' e-click='__leftClick'/>
                    <ui-button icon='double-arrow-left' e-click='__leftClick1'/>
                </div>

                <div class='ui-list'>
                    <div x-repeat={{__getRightRows()}} class={{'ui-list-item' + (__selected?' ui-list-item-active':'')  ${disableCtx} }} 
                        e-click='__clickItem'>
                        <div class='ui-list-itemcontent'>
                            <slot innerRender/>
                        </div>
                        <b class="ui-list-icon"></b>
                    </div>
                </div>
            </div>
        `;
    }

    onBeforeRender(){
        if(this.__value){
            this.__setValue(this.__value);
        }
    }

    /**
     * 获取左边列表数据
     * @returns 
     */
    private __getLeftRows():any[]{
        if(!this.model['data']){
            return;
        }
        return this.model['data'].filter(item=>!item.__valued);
    }

    /**
     * 获取右边列表数据
     * @returns 
     */
    private __getRightRows():any[]{
        if(!this.model['data']){
            return;
        }
        return this.model['data'].filter(item=>item.__valued);
    }

    /**
     * 点击item
     * @param model 
     * @param dom 
     * @returns 
     */
    private __clickItem(model,dom,evobj,e){
        if(this.__disableField && model[this.__disableField]){
            return;
        }
        model['__selected'] = !model['__selected'];
    }

    /**
     * 设置值
     * @param value 
     * @returns 
     */
    private __setValue(value){
        let rows = this.model['data'];
        if(!this.__field || !Array.isArray(rows)){
            return;
        }
        
        if(!Array.isArray(value)){
            value = [value];
        }
        for(let m of rows){
            if(value.indexOf(m[this.__valueField]) !== -1){
                m['__valued'] = true;
            }else{
                m['__valued'] = false;
            }
        }
        
        this.__value = value;
    }

    /**
     * 把选中节点传递到右边
     * @param model 
     * @param dom 
     */
    private __rightClick(model,dom){
        if(!this.model['data']){
            return;
        }
        let rows = this.model['data'].filter(item=>!item.__valued && item.__selected);
        if(rows){
            for(let r of rows){
                r.__valued = true;
            }
        }
        this.__updateValue();
    }

    /**
     * 把所有节点传递到右边
     * @param model 
     * @param dom 
     */
     private __rightClick1(model,dom){
        if(!this.model['data']){
            return;
        }
        for(let r of this.model['data']){
            if(!this.__disableField || !r[this.__disableField]){
                r.__valued = true;
            }
        }
        this.__updateValue();
    }

    /**
     * 把选中节点传递到左边
     * @param model 
     * @param dom 
     * @returns 
     */
    private __leftClick(model,dom){
        if(!this.model['data']){
            return;
        }
        let rows = this.model['data'].filter(item=>item.__valued && item.__selected);
        if(rows){
            for(let r of rows){
                r.__valued = false;
                r.__selected = false;
            }
        }
        this.__updateValue();
    }

    /**
     * 把所有节点传递到左边
     * @param model 
     * @param dom 
     * @returns 
     */
    private __leftClick1(model,dom){
        if(!this.model['data']){
            return;
        }
        for(let r of this.model['data']){
            r.__valued = false;
        }
        this.__updateValue();
    }

    /**
     * 更新值
     * @returns 
     */
    private __updateValue(){
        if(!this.model['data'] || !this.__valueField || !this.__field){
            return;
        }
        let v = []
        for(let r of this.model['data']){
            if(r.__valued){
                v.push(r[this.__valueField]);
                r.__selected = false;
            }
        }
        
        //设置值
        this.srcDom.model.$set(this.__field,v);
    }
}

registModule(UIListTransfer,'ui-listtransfer');