//关闭右键菜单
document.oncontextmenu = function (e) {
    e.preventDefault();
};

/**
 * 工具类
 */
export class UITool {
    /**
     * 去掉字符串的空格
     * @param src 
     */
    static clearSpace(src: string): string {
        if (src && typeof src === 'string') {
            return src.replace(/\s+/g, '');
        }
    }

    /**
     * 计算实际位置
     * @param el    待计算的element
     * @returns     [x坐标,y坐标]
     */
    static getRealPos(el:HTMLElement):number[]{
        let x = 0;
        let y = 0;
        while(el && el !== document.body){
            x += el.offsetLeft;
            y += el.offsetTop;
            el = <HTMLElement>el.offsetParent;
        }
        return [x,y];
    }

    /**
     * 计算位置
     * @param relEl         相对element
     * @param relPos        相对位置：1下 2右
     * @param width         dom宽
     * @param height        dom高
     * @returns             [x,y]
     */
    static cacPosition(relEl:HTMLElement,relPos:number,width:number,height:number):number[]{
        //参考dom宽高
        const relW = relEl.offsetWidth;
        const relH = relEl.offsetHeight;
        const padding=2;
        let x=0;
        let y=0;
        let gwidth = document.documentElement.clientWidth;
        let gheight = document.documentElement.clientHeight;
        let realXy = UITool.getRealPos(relEl);
        
        if(relPos === 1){
            if(realXy[1] + height + relH>gheight && realXy[1] - height>0){ //如果超出下边，则选择放在上边
                y = -height - padding*2;
            }else{
                y = relH;
            }
            if(realXy[0] + width > gwidth){
                x = -width+relW;
            }
        }else{
            if(realXy[0] + width + width>gwidth && realXy[0] - width>0){ //如果超出右边，则选择放在左边
                x = -width - padding*2;
            }else{
                x = relW;
            }
            if(realXy[1] + height>gheight){ //如果超出下边，则上移
               y = -height + relH; 
            }
        }
        return [x,y];
    }
}