import { Module } from "nodom3";

export class BaseInput extends Module{
    /**
     * 对应字段
     */
    protected __field:string;

    /**
     * change事件方法名
     */
     __onChangeName:string;

    template(props){
        this.__field = props.field;
        this.__onChangeName = props.onChange;
        return null;
    }

    onBeforeFirstRender(model){
        const me = this;
        //增加监听
        if(this.__field){
            this.srcDom.model.$watch(this.__field,(m,ov,nv)=>{
                me.__change(nv);
            });
        }
    }
    /**
     * 更改值
     * 需要对父模块对应数据项进行更改
     * @param value     新值
     */
    public __change(value:any,updSrc?:boolean){
        if(!this.__field){
            return;
        }
        let v1 = this.model['__value'];
        if(value===v1){
            return;
        }
        //调用change事件
        if(this.__onChangeName){
            this.getParent().invokeMethod(this.__onChangeName,v1,value);
        }

        this.model['__value'] = value;
        
        //更改父模块对应数据项
        this.srcDom.model.$set(this.__field,value);
    }

    /**
     * 每次刷新前获取新值
     */
    public onBeforeRender(){
        this.__initValue();
    }
    /**
     * 初始化value
     * @returns 
     */
    public __initValue(){
        if(!this.__field){
            return;
        }
        const v = this.srcDom.model.$get(this.__field);
        const v1 = this.model['__value'];
        if(v1 === v){
            return;
        }
        this.model['__value'] = v;
    }

    /**
     * 获取值
     * @returns     模块值 
     */
    public __getValue(){
        return this.model['__value'];
    }
}