
import {Module, registModule} from "nodom3";
import { BaseInput } from "./baseinput";
/**
 * checkbox插件
 * 配置说明
 * field        绑定父模块的字段
 * onChange     change事件方法
 */
export class UICheckbox extends BaseInput{
    

    template(props){
        super.template(props);
        
        return `
            <span class='ui-checkbox'>
                <b class={{__value?'ui-checkbox-checked':'ui-checkbox-uncheck'}} e-click='__clickCheck'/>
                ${props.title}
            </span>
        `
    }

    __clickCheck(model){
        model['__value'] = !model['__value'];
        //设置值到父对象
        if(this.__field){
            this.srcDom.model.$set(this.__field, model['__value']);
        }
    }

    public __change(value: any, updSrc?: boolean): void {
        
    }
}
registModule(UICheckbox,'ui-checkbox');